import React from "react";

class Registration extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      email: "",
      password: "",
      firstName: "",
      lastName: "",
      age: 0,
      alertColor: "black",
      showError: false,
      missingFields: "Please fill in the missing fields",
    };
  }

  validateInput = () => {
    if (this.state.firstName.length !== 0 && this.state.lastName.length !== 0) {
      return true;
    } else {
      this.setState({ alertColor: "#f55e67", showError: true });
      return false;
    }
  };

  handleInputChange = ({ target }) => {
    const value = target.type === "checkbox" ? target.checked : target.value;
    this.setState({
      [target.name]: value,
    });
  };

  handleSignUp = () => {
    /*
        -import signup service

        allow that service to handle creating new user


        ...
        ... await
        ...

        push user to welcome page
      */
    if (this.validateInput()) {
      this.props.toggleWelcomeScreen();
    }
  };
  render() {
    return (
      <div className="container pt-5 text-center">
        <div className="BrandingContainer mb-5">
          <i>
            <h4>
              First, We need a few more details about <b>you</b>.
            </h4>
          </i>
        </div>

        <div className="FirstNameInputContainer mb-4">
          <div className="row">
            <div className="col-2"></div>
            <h6 style={{ color: this.state.alertColor }}>First Name</h6>
          </div>
          <div className="row">
            <div className="col-2"></div>
            <input
              className="col"
              type="text"
              placeholder="John"
              name="firstName"
              id="FirstNameInput"
              onChange={this.handleInputChange}
            />
            <div className="col-2"></div>
          </div>
        </div>

        <div className="PasswordInputContainer mb-4">
          <div className="row">
            <div className="col-2"></div>
            <h6 style={{ color: this.state.alertColor }}>Last Name:</h6>
          </div>
          <div className="row">
            <div className="col-2"></div>
            <input
              className="col"
              type="text"
              placeholder="Smith"
              name="lastName"
              id="LastNameInput"
              onChange={this.handleInputChange}
            />
            <div className="col-2"></div>
          </div>
        </div>

        <div className="AgeInputContainer mb-3">
          <div className="row">
            <div className="col-2"></div>
            <h6 style={{ color: this.state.alertColor }}>Age:</h6>
          </div>
          <div className="row">
            <div className="col-2"></div>
            <input
              className="col"
              type="number"
              placeholder="44"
              name="age"
              id="AgeInput"
              onChange={this.handleInputChange}
            />
            <div className="col-2"></div>
          </div>
        </div>

        <div className="form-check mb-4 row">
          <div className="col">
            <input
              name="termsAndServices"
              type="checkbox"
              className="form-check-input"
              id="MaleInput"
              onChange={this.handleInputChange}
            />
            Male
          </div>
          <div className="col">
            <input
              name="termsAndServices"
              type="checkbox"
              className="form-check-input "
              id="FemaleInput"
              onChange={this.handleInputChange}
            />
            Female
          </div>
          <div className="col">
            <input
              name="termsAndServices"
              type="checkbox"
              className="form-check-input"
              id="UndisclosedInput"
              onChange={this.handleInputChange}
            />
            Pefer not to Disclose
          </div>
        </div>

        {this.state.showError ? (
          <p style={{ color: this.state.alertColor }}>
            Invalid {this.state.missingFields}
          </p>
        ) : null}

        <div className="SignInContainer row mb-4">
          <div className="col-2"></div>
          <div
            className="btn btn-primary col"
            onClick={this.handleSignUp}
            id="ContinueRegistrationButton"
            style={{ backgroundColor: this.state.alertColor }}
          >
            Continue
          </div>
          <div className="col-2"></div>
        </div>
      </div>
    );
  }
}

export default Registration;
