import React from "react";
import WelcomePage from "../components/Welcome/WelcomePage";
import Registration from "../components/LogIn/Registration/Registration";
import Login from './Login'

class PatientHomePage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      page: "signin",
      email: "",
      password: "",
    };
  }

  renderComponent() {
    switch (this.state.page) {
      case "welcome":
        return <WelcomePage />;
      case "register":
        return (
          <Registration
            toggleWelcomeScreen={this.toggleWelcomeScreen}
            email={this.state.email}
            password={this.state.password}
          />
        );
      default:
        return (
          <Login 
            toggleWelcomeScreen={this.toggleWelcomeScreen}
            toggleRegistrationScreen={this.toggleRegistrationScreen}
          />
        );
    }
  }

  toggleWelcomeScreen = (email) => {
    this.setState({
      email,
      page: "welcome",
    });
  };

  toggleRegistrationScreen = (email, password) => {
    this.setState({
      email,
      password,
      page: "register",
    });
  };

  render() {
    return <div>{this.renderComponent()}</div>;
  }
}

export default PatientHomePage;
