import React from "react";
import "./SignUp.css";

class SignUp extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <div>
        <div className="PageContainer mb-4">
          <h4>Sign Up</h4>
          <small className="text-muted">Hi there! Nice to meet you.</small>
        </div>

        <div className="EmailInputContainer mb-4">
          <div className="row">
            <div className="col-2"></div>
            <h6 style={{ color: this.props.loginState.alertColor }}>Email:</h6>
          </div>
          <div className="row">
            <div className="col-2"></div>
            <input
              className="col"
              type="text"
              placeholder="email@email.com"
              name="email"
              onChange={this.props.handleInputChange}
              id="InputEmail"
            />
            <div className="col-2"></div>
          </div>
        </div>

        <div className="PasswordInputContainer mb-4">
          <div className="row">
            <div className="col-2"></div>
            <h6 style={{ color: this.props.loginState.alertColor }}>
              Password:
            </h6>
          </div>
          <div className="row">
            <div className="col-2"></div>
            <input
              className="col"
              type="password"
              placeholder="*********"
              name="password"
              onChange={this.props.handleInputChange}
              id="InputPassword"
            />
            <span className="material-icons underlineIcon">visibility</span>
            <div className="col-2"></div>
          </div>
        </div>

        <div>
          <div className="form-check mb-3 row">
            <div className="col">
              <input
                name="termsAndServices"
                type="checkbox"
                className="form-check-input"
                id="TermsAndServCheckBox"
                onChange={this.props.handleInputChange}
              />
              I agree to the{" "}
              <b style={{ color: this.props.loginState.alertColor }}>
                Terms of Services
              </b>{" "}
              and{" "}
              <b style={{ color: this.props.loginState.alertColor }}>
                Privacy Policy
              </b>
              .
            </div>
          </div>

          {this.state.showError ? (
            <p style={{ color: this.props.loginState.alertColor }}>
              {this.props.loginState.invalidField}
            </p>
          ) : null}
          <div className="SignInContainer row mb-3">
            <div className="col-2"></div>
            <div
              className="btn btn-primary col"
              onClick={this.props.continueSignUp}
              style={{ backgroundColor: this.props.loginState.alertColor }}
              id="ContinueSignUpButton"
            >
              Continue
            </div>
            <div className="col-2"></div>
          </div>

          <div className="ExtraButtons row">
            <div className="col" onClick={this.props.changeToSignIn}>
              Already have an account?
              <span className="text-danger pl-2" role="button">
                Sign In
              </span>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default SignUp;
