import React from "react";

import Cloud from '../resources/cloud1.svg'


import SignIn from "../components/LogIn/SignIn/SignIn";
import SignUp from "../components/LogIn/SignUp/SignUp";

class LogIn extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      signUp: false,
      email: "",
      password: "",
      termsAndServices: false,
      alertColor: "black",
      invalidField: "",
      showError: false,
    };
  }
  changeToSignIn = () => {
    this.setState({
      signUp: false,
    });
  };

  changeToSignUp = () => {
    this.setState({
      signUp: true,
    });
  };

  handleSignIn = () => {
    if (!this.state.showError) {
      this.props.toggleWelcomeScreen(this.state.email);
    }
  };

  continueSignUp = () => {
    if (!this.state.showError) {
      this.props.toggleRegistrationScreen(
        this.state.email,
        this.state.password
      );
    }
  };

  handleInputChange = ({ target }) => {
    const value = target.type === "checkbox" ? target.checked : target.value;

    const emailRegex = new RegExp(".com$");
    const pwdRegex = new RegExp("^(?=.*[0-9])(?=.{8,})");

    if (target.name === "email") {
      if (!emailRegex.test(target.value)) {
        this.setState({
          invalidField: "Email",
          alertColor: "#f55e67",
          showError: true,
        });
      } else {
        this.setState({
          alertColor: "black",
          showError: false,
        });
      }
    }

    if (target.name === "password") {
      if (!pwdRegex.test(target.value)) {
        this.setState({
          invalidField: "Password",
          alertColor: "#f55e67",
          showError: true,
        });
      } else {
        this.setState({
          alertColor: "black",
          showError: false,
        });
      }
    }

    this.setState({
      [target.name]: value,
    });
  };

  render() {
    return (
      <div className="container text-center">

        <div className="BrandingContainer mb-4">
          <img className="mb-2 CloudIconClass" src={Cloud} alt=" filler" />
          <h2>Health Connect</h2>
        </div>

        {this.state.signUp ? (
          <SignUp
            changeToSignIn={this.changeToSignIn}
            handleInputChange={this.handleInputChange}
            continueSignUp={this.continueSignUp}
            loginState={this.state}
          />
        ) : (
          <SignIn
            changeToSignUp={this.changeToSignUp}
            handleInputChange={this.handleInputChange}
            handleSignIn={this.handleSignIn}
            loginState={this.state}
          />
        )}
      </div>
    );
  }
}

export default LogIn;
