import React from "react";
import "./App.css";

import PatientHomePage from "./Patient/views/PatientHomePage";

import PraticonerHomePage from "./Praticoner/views/PraticonerHomePage";


import { HashRouter, Route } from "react-router-dom";


function App() {
  return (
    <div className="App">
      <HashRouter basename="/">
          <Route path="/patient">
            <PatientHomePage />
          </Route>
          <Route path="/provider">
            <PraticonerHomePage/>
          </Route>
      </HashRouter>
    </div>
  );
}

export default App;
