import React from "react";

function StandardAlertSelect(props) {
  return (
    <div>
      <h4>
        Welcome {props.Provider.name}
        <br />
        You have:
      </h4>
      <h4 className="text-danger">{props.AlertArray.length} Alerts</h4>
      <div>
        {props.AlertArray.map((element, i) => {
          return (
            <h5 className="text-danger" key={i} onClick={()=>props.togglePatientDetails(element,false)}>
              {element.patient.firstName + " " + element.patient.lastName}
            </h5>
          );
        })}
      </div>

      <h4 className="mt-5">{props.ReadingArray.length} New Readings</h4>
    </div>
  );
}

export default StandardAlertSelect;
