import React from "react";

function UrgentAlertSelect(props) {
  return (
    <div>
      <h3>{props.Provider.name}</h3>
      <div className="text-danger">

        {
            (props.UrgentAlertArray.length > 1)
            ?   <h4>Urgent Alerts</h4>
            :   <h4>Urgent Alert</h4>

        }


        {props.UrgentAlertArray.map((element, i) => {
          return (
            <div className="card p-3 mt-5 mb-5" key={i} onClick={()=>props.togglePatientDetails(element,true)}>
              <h4>{element.patient.firstName} {element.patient.lastName}</h4>
              <h4>{element.patient.limits.AlertMsg}</h4>
            </div>
          );
        })}
      </div>
    </div>
  );
}

export default UrgentAlertSelect;
