import React from "react";

import Calendar from "react-calendar";
import "react-calendar/dist/Calendar.css";

import TimePicker from "react-time-picker";
import "react-time-picker/dist/TimePicker.css";

import Cloud from "../../resources/cloud1.svg";

import "./Schedule.css";

class Schedule extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      date: new Date(),
      dateForDisplay: "",
      time: "10:00",
    };
  }

  onChangeCalendar = (date) =>
    this.setState({
      date: date,
      dateForDisplay: Intl.DateTimeFormat("en-US", {
        year: "numeric",
        month: "short",
        day: "2-digit",
      }).format(date),
    });

  onChangeTime = (time) => this.setState({ time });

  render() {
    return (
      <div className="container text-center">
        <div className="BrandingContainer">
          <img className="CloudIconResize" src={Cloud} alt=" filler" />
        </div>
        <h4>Your Calendar</h4>
        <div className="">
          <small className="text-muted"> showing earliest available</small>
        </div>

        <div className="jumbotron text-center">
          <div className="row">
            <div className="col"></div>
            <Calendar
              onChange={this.onChangeCalendar}
              value={this.state.date}
              className="col-3 col-xs-12 overwriteCalendarCss"
            />
            <div className="col"></div>
          </div>

          <div>
            <TimePicker onChange={this.onChange} value={this.state.time} />
          </div>

          <div
            className="btn btn-primary mt-2"
            onClick={this.props.togglePatientDetails}
          >
            schedule appointment
          </div>

          <div>
            <small className="text-muted mt-4">
              Patient will be notified of time and date.
            </small>
          </div>
        </div>

        <div
          className="btn btn-secondary"
          onClick={this.props.togglePatientDetails}
        >
          back to patient details
        </div>
      </div>
    );
  }
}

export default Schedule;
