import React from "react";

import AlertView from "../components/AlertView/AlertView";
import UrgentDetails from "../components/Urgent/UrgentDetails";
import PatientDetails from "../components/PatientDetails/PatientDetails";
import Schedule from "../components/Calendar/Schedule";
import WelcomePraticoner from "../components/WelcomePage/WelcomePraticoner";
import ProviderLogin from "./ProviderLogin";
import Registration from "../components/LogIn/Registration/Registration";

class PraticonerHomePage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      page: "",
      email: "",
      password: "",
      urgentAlert: false,
      currentPatient: {},
    };
  }

  componentDidMount() {
    if (this.state.urgentAlert) {
      /*
            this will eventually be loading in dynamically
        */
      this.setState({
        page: "urgent",
      });
    }
  }

  renderComponent() {
    switch (this.state.page) {
      case "welcome":
        return <WelcomePraticoner toggleAlertView={this.toggleAlertView} />;
      case "alertView":
        return (
          <AlertView
            toggleUrgentDetails={this.toggleUrgentDetails}
            togglePatientDetails={this.togglePatientDetails}
          />
        );

      case "urgent":
        return <UrgentDetails toggleAlertView={this.toggleAlertView} />;

      case "patientDetails":
        return (
          <PatientDetails
            isUrgent={this.state.urgentPatient}
            patient={this.state.currentPatient}
            toggleAlertView={this.toggleAlertView}
            toggleCalendar={this.toggleCalendar}
          />
        );

      case "calendar":
        return <Schedule togglePatientDetails={this.togglePatientDetails} />;
      case "register":
        return (
          <Registration
            toggleWelcomeProviderScreen={this.toggleWelcomeProviderScreen}
            email={this.state.email}
            password={this.state.password}
          />
        );
      default:
        return (
          <ProviderLogin
            toggleWelcomeProviderScreen={this.toggleWelcomeProviderScreen}
            toggleRegistrationScreen={this.toggleRegistrationScreen}
          />
        );
    }
  }

  toggleAlertView = () => {
    this.setState({
      page: "alertView",
    });
  };

  toggleUrgentDetails = () => {
    this.setState({
      page: "urgent",
    });
  };

  togglePatientDetails = (patient,urgent) => {
    this.setState({
      urgentAlert: urgent,
      currentPatient: patient,
      page: "patientDetails"
    });
  };

  toggleCalendar = () => {
    this.setState({
      page: "calendar",
    });
  };

  toggleWelcomeProviderScreen = (email) => {
    this.setState({
      email,
      page: "welcome",
    });
  };

  toggleRegistrationScreen = (email, password) => {
    this.setState({
      email,
      password,
      page: "register",
    });
  };

  render() {
    return <div>{this.renderComponent()}</div>;
  }
}

export default PraticonerHomePage;
