import React, { useEffect, useState } from "react";
import Heart from "../../../resources/heart-beat.svg";
import "./DeviceSync.css";

function DeviceSync(props) {
  console.log(props);
  const [status, changeStatus] = useState("Connecting");

  useEffect(() => {
    let readyToLoad = false;
    const timer = setInterval(() => {
      console.log("timer called");
      changeStatus("Reading");
      if (readyToLoad) {
        clearTimeout(timer);
        changeStatus("Loaded");
        props.handleAction("loaded", props.device);
      }
      readyToLoad = true;
    }, 2000);
    return () => {
      clearTimeout(timer);
    };
  }, [props]);

  return (
    <div className="container-fluid text-center">
      <div className="BrandingContainer mb-4 mt-3">
        <img
          className="img-fluid mt-5"
          src={Heart}
          alt="Health Logo"
          style={{ width: "220px", height: "180px" }}
        />
      </div>

      <div className="PageContainer font-weight-bold mb-5">
        <p className="m-2 pl-0 pr-0 p-1 h4">
          Make sure your {props.device} Monitoring Device is connected to your
          handset via BlueTooth
        </p>
      </div>
      <div className="OptionContainer row mb-4"></div>
      <div className="row  font-weight-bold">
        <div className="col-2"></div>

        {/* device connection status */}
        <h5 className=" col p-2 loading">
          {status}
          <span className="one">.</span>
          <span className="two">.</span>
          <span className="three">.</span>
        </h5>
        <div className="col-2"></div>
      </div>
    </div>
  );
}

export default DeviceSync;
